import React, { Component } from "react";
import { connect } from "react-redux";
import { filterRecentviewed, viewRecent } from "../actions/auth";
var data = [];
class Chart extends Component {
  state = {};
  componentDidMount() {
    this.props.dispatch(viewRecent());
  }
  custom = (e) => {
    var images = [];
    data = [];
    images = this.props.recentItems;
    var top = e.target.value;
    top = parseInt(top);
    //console.log(top);
    if (top == 0) {
      /* for (var i = images.length; i > 0; i--) {
        data = [...data, images[i - 1]];
      } */
      data = images;
      // console.log(data);
    } else if (images.length <= top) {
      for (var i = images.length; i > 0; i--) {
        data = [...data, images[i - 1]];
      }
    } else {
      for (var i = top; i > 0; i--) {
        data = [...data, images[i - 1]];
      }
    }
    //console.log(data);
    this.props.dispatch(filterRecentviewed(data));
  };
  render() {
    let recentview;
    if (this.props.filteredRecent) {
      recentview = this.props.filteredRecent.map((item) => {
        return (
          <div
            className="thumbnail "
            style={{
              float: "left",
              width: "22%",
              padding: "2%",
            }}
          >
            <img
              src={item.img}
              alt="image"
              style={{ width: "250px", height: "250px" }}
            ></img>
          </div>
        );
      });
    }

    return (
      <div className="container">
        {this.props.filteredRecent.length != 0 ? (
          <div>
            <div class="row">
              <h1
                style={{
                  fontSize: "30px",
                  fontFamily: "Century Gothic",
                }}
              >
                Recently Viewed products
                <br />
                <span style={{ fontSize: "40px" }}>
                  <strong>
                    <span style={{ marginLeft: "10px" }}></span> ___
                  </strong>
                </span>
              </h1>
            </div>
            <br />
            <div className="row">
              <div className="col-md-4">
                <select onClick={this.custom} className="form-control">
                  <option value="0">All</option>
                  <option value="3">Top 3</option>
                  <option value="5">Top 5</option>
                </select>
              </div>
            </div>
          </div>
        ) : (
          <div></div>
        )}
        <div className="row">
          <div className="col-md-12">{recentview}</div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    recentItems: state.cartReducer.recentItems,
    filteredRecent: state.cartReducer.filteredrecentItems,
  };
};
export default connect(mapStateToProps)(Chart);
